* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
  background-color: aliceblue;
  box-shadow: 0 3px 5px #e3eef8;
}
.navbar .nav-links {
  display: flex;
  list-style: none;
}
.navbar .nav-links li {
  padding: 0 10px;
}
.navbar .nav-links .link {
  text-decoration: none;
}
.about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  margin: 50px auto;
  gap: 20px;
}
.error-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin: 50px auto;
  width: 300px;
}
