.cocktail {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}
.cocktail img {
  height: 200px;
  width: 300px;
  object-fit: cover;
}
.cocktail .cocktail-footer {
  padding: 10px 10px 5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.cocktail .cocktail-footer .btn-details {
  text-decoration: none;
  border: 1px solid black;
  border-radius: 3px;
  background: #065906;
  padding: 5px 3px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: white;
}
