.loading {
  display: flex;
  gap: 10px;
}
.loadingpart {
  width: 50px;
  height: 100px;
  background-color: #065d06;
}
.loading-p1 {
  animation: rotate1 1s infinite ;
}
.loading-p2 {
  animation: rotate2 1s 0.5s infinite;
}
.loading-p3 {
  animation: rotate3 1s 1s infinite ;
}
@keyframes rotate1 {
  0% {
    height: 0px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: 0px;
  }
}
@keyframes rotate2 {
  00% {
    height: 0px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: 0px;
  }
}
@keyframes rotate3 {
  0% {
    height: 0px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: 0px;
  }
}
