* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.section-title {
  text-transform: capitalize;
}
.cocktail-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}
.cocktail-section .btn-primary {
  text-decoration: none;
  border: 1px solid black;
  border-radius: 3px;
  background: #065906;
  padding: 5px 3px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: white;
}
.cocktail-section .drink {
  display: flex;
  align-items: center;
  gap: 20px;
}
.cocktail-section .drink img {
  width: 300px;
  height: 300px;
  object-fit: cover;
}
.cocktail-section .drink p {
  display: flex;
  gap: 5px;
  margin: 10px 0;
  font-weight: bolder;
}
.cocktail-section .drink p .drink-data {
  font-weight: 100;
  text-transform: capitalize;
  background-color: #90d22c;
  padding: 2px;
  border-radius: 2px;
}
