.search-form {
  width: 400px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 3px 3px 2px 1px rgba(0, 0, 0, 0.3);
  margin: 100px auto;
  background-color: #fffafa;
  gap: 20px;
}
.form-control {
  text-transform: capitalize;
  font-weight: bolder;
}
input {
  border: none;
  outline: none;
  background-color: #dee8f0;
  width: 200px;
  height: 20px;
}
.section-title {
  text-transform: capitalize;
}
.cocktail-section {
  display: flex;
  flex-direction: column;
}
